<template>
  <div>
    <b-container :class="containerClass">
      <b-row>
        <b-col md="12" class="text-center" v-if="is_loading">
          <b-spinner variant="primary" label="Spinning"></b-spinner>
        </b-col>
        <b-col md="12" class="text-center" v-if="!is_loading && !tools.length && !is_document_expired">
          <p>No Records Found.</p>
        </b-col>
        <b-col md="12" class="text-center" v-if="is_document_expired && !is_loading">
          <p>Your document has Expired!!.</p>
        </b-col>
        <b-modal
          ref="confirm-modal"
          id="confirm-modal"
          no-close-on-esc
          v-if="!is_loading && this.sign_key && this.sign_key !== 'null'"
          no-close-on-backdrop
          hide-header-close
          title="Please enter the secret code"
          hide-footer
          centered
        >
          <SignAuthenticationForm :sign_key="sign_key" @authenticationSuccess="authenticationSuccess" />
        </b-modal>
        <b-col lg="12" :class="active_user.length && active_user[0].status == 'Signature Pending' ? '' : 'mobile-ac-esign-body'">
          <Esign
            :max_image_size="525"
            :src="s3_url"
            :s3_config="s3_config_object"
            :users_list="users"
            :edit_tools="edit_tools"
            :active_users_list="active_user"
            :show_save_button="show_save_button"
            ref="esign_component_ref"
            @input="save($event)"
            @show_comments="showComments"
            :is_public="true"
            v-if="is_authenticated && tools.length"
            v-model="tools"
          >
            <template #process_loader>
              <div class="text-center">
                <b-spinner variant="primary" label="Spinning"></b-spinner>
              </div>
            </template>
            <template #viewer_loader>
              <div class="text-center">
                <b-spinner variant="primary" label="Spinning"></b-spinner></div
            ></template>
            <template #thumbnails_loader>
              <div class="text-center">
                <b-spinner variant="primary" label="Spinning"></b-spinner></div
            ></template>
          </Esign>
        </b-col>

        <div
          class="align-items-center bg-white border-left border-right border-top d-block d-md-flex  justify-content-between mt-1 mx-0 mx-md-3 px-3 py-3 w-100"
          v-if="is_authenticated && tools.length && active_user.length && active_user[0].status == 'Signature Pending'"
        >
          <p class="mb-0 small"><strong>Note: </strong>Once data is submitted, it cannot be undone. Please recheck before you Finish.</p>

          <div>
            <span class="badge badge-soft-danger mr-3 px-3" :class="status ? 'custom-toast' : 'custom-toast-close'">{{ status }}</span
            ><b-button variant="primary" size="sm" class="mr-3 mt-2 px-4 float-lg-none float-right" @click="saveDocument" :disabled="button_disabled">
              Finish <b-spinner v-if="button_disabled" small label="Spinning small"></b-spinner
            ></b-button>
          </div>
          <!-- <div  class="text-right pt-3 mr-3">
           
          </div> -->
        </div>
        <SaveResponseMessage v-if="show_response" />
        <div class="border-top sign-footer p-2 text-right w-100 mx-3" :class="active_user.length && active_user[0].status == 'Signature Pending' ? '' : 'sign-fixed-footer'" v-if="is_authenticated">
          <div class="small pr-4 my-1">
            <span class="">Powered by</span
            ><a target="_blank" class="text-primary ml-1" href="http://500apps.com/"
              ><img
                src="/img/favicon.webp"
                style="
    width: 17px;
"
                class="ml-2"
              />
              500apps</a
            >
          </div>
        </div>
      </b-row>
      <b-sidebar
        id="comments_slideout"
        lazy
        title="Comments"
        header-class="d-flex justify-content-between flex-row-reverse bg-white
          border-bottom rounded-0 close-btn-right font-size-base"
        body-class="bg-soft-light rounded-0"
        width="600px"
        right
        shadow
        backdrop
        backdrop-variant="dark"
        :no-close-on-backdrop="true"
      >
        <Comments v-bind="{ show_comments, comments, project_id: document_signees.project_id, users, contact_name: active_user[0] && active_user[0].name ? active_user[0].name : '', admin }" />
      </b-sidebar>
    </b-container>
  </div>
</template>

<script>
  import Esign from "./esign/AcEsign";
  import SignAuthenticationForm from "./SignAuthenticationForm";
  import SaveResponseMessage from "./SaveResponseMessage";
  import Documents from "@/js/sign/documents.js";
  import Data from "@/js/core/data";
  import Comments from "./Comments";
  import CommentJS from "@/js/sign/comments.js";
  export default {
    components: { Esign, SignAuthenticationForm, SaveResponseMessage, Comments },
    data() {
      return {
        comments: [],
        is_loading: true,
        document_signees: {},
        button_disabled: false,
        show_response: false,
        is_authenticated: false,
        s3_config_object: {
          url: "",
          id: "file_id",
          name: "file_name",
        },
        users: [],
        edit_tools: ["text", "date", "signature"],
        show_save_button: false,
        active_user: [],
        tools: [],
        s3_url: "",
        db_data: {},
        result: "",
        sign_key: "",
        data: new Data(),
        status: "",
        total_tools: [],
        admin: "",
        show_comments: true,
        other_user_tools: [],
        is_document_expired: false,
      };
    },
    methods: {
      // Get Document Expiry details
      async getDocumentExpiryDetails() {
        this.is_loading = true;
        this.is_document_expired = false;
        let response = await Documents.get_document_expiry_details(window.domain, window.user, this.$route.params.document_id);
        // Check response message has expired then doesn't allow the sign component.
        if (response.message.includes("Expired")) {
          this.is_loading = false;
          this.is_document_expired = true;
        }
        // Show No records found
        else if (response.message.includes("Document does not exist")) this.is_loading = false;
        // Allow the sign component
        else {
          await this.getDocumentSignees();
          if (this.sign_key && this.sign_key !== "null" && this.active_user[0].status == "Signature Pending") this.$refs["confirm-modal"].show();
          else if (Object.keys(this.document_signees).length && this.document_signees.constructor === Object) this.is_authenticated = true;
          await this.getComments();
        }
      },
      authenticationSuccess() {
        this.is_authenticated = true;
        this.$refs["confirm-modal"].hide();
      },
      // Get Document Signees Details
      async getDocumentSignees() {
        try {
          this.document_signees = await Documents.get_document_signees(this.$route.params.document_id, window.domain);
          this.admin = this.document_signees.admin_name;
          this.active_user = JSON.parse(this.document_signees.signee_details).filter((document_signee) => document_signee.id == this.$route.params.contact_id);
          this.s3_url = this.data.getPCors(this.document_signees.url);
          this.s3_config_object.name = this.document_signees.url.split("/").slice(-1)[0];
          this.users = JSON.parse(this.document_signees.signee_details);
          this.sign_key = this.active_user[0].sign_key;
          this.total_tools = JSON.parse(this.document_signees.doc_json).tools;
          // If status of the document is pending then we are showing only current user tool.
          if (this.document_signees.status == "Signature Pending")
            this.total_tools.forEach((tool) => {
              //  show only currert user tools in UI
              if (tool.user_id == this.$route.params.contact_id) this.tools.push(tool);
              //  Remaining  user tools
              else this.other_user_tools.push(tool);
            });
          // Once All Signees are signed then we are showing all tools.
          else this.tools = this.total_tools;

          this.is_loading = this.tools.length ? false : true;
        } catch (error) {
          this.is_loading = true;
          console.log(error);
        }
      },
      // Save the tools data
      async save($event) {
        try {
          this.button_disabled = true;
          this.tools = $event;
          this.db_data = {};
          let doc_json = {};
          // Updating current user tool changes and send to DB
          doc_json.tools = [...this.other_user_tools, ...$event];
          this.db_data.doc_json = [];
          this.db_data.order_id = this.active_user[0].signing_order;
          // If Current user has signing order then we are sending comment in mail
          if (this.active_user[0].signing_order && this.comments.length) this.db_data.comment = this.comments[0].comments;

          this.db_data.template_url = `${window.location.origin}/${this.$route.name.toLowerCase()}/${this.$route.params.document_id}`;
          this.db_data.doc_json.push(doc_json);
          this.db_data.doc_json = JSON.stringify(this.db_data.doc_json);
          this.db_data.doc_json = this.db_data.doc_json.substring(1, this.db_data.doc_json.length - 1);
          this.db_data.status = "Signed";
          this.db_data.is_all_users_signed = 0;

          // If Current user has signing order & authentication key then we are sending both in mail.
          if (this.sign_key) this.db_data.auth_key = this.sign_key;

          // Check user tools are empty value or not
          var tool_validation = false;
          this.tools.forEach((tool) => {
            if (tool.user_id == this.$route.params.contact_id && !tool.props?.value) {
              tool_validation = true;
              this.status = "Please fill all the tools before submitting the document";
            }
          });
          if (tool_validation) {
            setTimeout(() => {
              this.button_disabled = false;
              this.status = "";
            }, 1500);
            return;
          }
          let current_user = this.users.filter((user) => user.status !== "Signed");
          // Updating the document status to 'Signed' once the all users signed
          if (current_user.length == 1 && current_user[0].id == this.active_user[0].id) {
            this.db_data.is_all_users_signed = 1;
          }
          let response = await Documents.update_document_data(
            this.document_signees.project_id,
            window.domain,
            this.$route.params.document_id,
            this.$route.params.contact_id,
            this.db_data,
            window.user,
          );
          if (response?.status) {
            setTimeout(() => {
              this.button_disabled = false;
              this.is_authenticated = false;
              this.show_response = true;
              this.status = "";
            }, 1500);
          }
        } catch (error) {
          console.log(error);
          this.button_disabled = false;
        }
      },
      // Save the signees document data in DB
      saveDocument() {
        this.$refs.esign_component_ref.$refs["ac-esign-toolbar"].save();
      },
      showComments() {
        this.$root.$emit("bv::toggle::collapse", "comments_slideout");
        this.getComments();
      },
      async getComments() {
        try {
          this.show_comments = true;
          let result = await CommentJS.get_signee_comments(this.$route.params.document_id, window.domain, this.document_signees.project_id);
          this.comments = result.length ? JSON.parse(result) : [];
        } catch (error) {
          console.log(error);
        }
        this.show_comments = false;
      },
    },
    computed: {
      containerClass() {
        return `mt-4 ${this.active_user.length && this.active_user[0].status == "Signature Pending" ? "" : "mb-4"}`;
      },
    },
    async mounted() {
      await this.getDocumentExpiryDetails();
    },
  };
</script>

<style>
  @import url("/css/sign/sign.css?v=infinity_release_tag");
</style>
