<template>
  <ToolWrapper
    v-model="is_edit"
    :resize="is_resizable"
    :resize_props="resize_props"
    :class="[class_name, 'ac-esign-tool-restriction']"
    :editable.sync="is_editable"
    :style="!value_dup ? 'border: 2px dashed #707070' : 'border: none'"
    :users_list="users_list"
    :is_admin="is_admin"
    ref="wrapper"
    @enter="stop_edit"
    @remove="remove_inner"
    @copy_tool="copy_tool"
  >
    <div v-if="!is_filled" class="ac-esign-tool-restriction_placeholder d-flex align-items-center justify-content-center" :style="no_resize_styles">
      <b-button variant="primary" size="sm px-3" @click="fill_restriction" :disabled="!is_editable">
        {{ inner_label }}
      </b-button>
    </div>
    <component
      v-else
      :is="inner_component_name"
      @isFilled="isFilled"
      :label="inner_label"
      @reset="resetTool"
      v-bind="get_inner_component_props()"
      :style="no_resize_styles"
      @resize-props="resizeProps"
      @remove_border="removeBorder"
      ref="inner_component"
      @clear_tool_sizes="clear_tool_sizes"
      @delete="remove_inner"
    />
  </ToolWrapper>
</template>

<script>
  import ToolWrapper from "./AcEsignToolWrapper.vue";
  import { SIMPLE_TOOLS } from "../constants";
  import { TOOLS } from "./index";
  import AcEsignToolSignature from "./AcEsignToolSignature.vue";
  import AcEsignToolText from "./AcEsignToolText.vue";
  import AcEsignToolDate from "./AcEsignToolDate.vue";
  /**
   * Ac Esign Tool Text - tool of input text`
   *`
   * @requires ToolWrapper
   */
  export default {
    name: "AcEsignToolResctriction",

    components: {
      ToolWrapper,
      AcEsignToolSignature,
      AcEsignToolText,
      AcEsignToolDate,
    },

    props: {
      users_list: {
        type: Array,
        required: false,
        default: () => [],
      },

      /**
       * Current selected user
       */
      user_id: [Number, String],

      class_name: [String, Array, Object],

      inner_component: [Number, Object],

      value: [Object, String],

      width: {
        type: Number,
        default: 200,
      },

      height: {
        type: Number,
        default: 200,
      },

      is_resizable: {
        type: Boolean,
        default: true,
      },

      is_admin: {
        type: Boolean,
        default: false,
      },

      parent_ref: Object,
      unique_id: String,

      label: String,

      editable: {
        type: Boolean,
        default: true,
      },
    },

    computed: {
      resize_props() {
        return {
          width: this.width,
          height: this.height,
        };
      },

      no_resize_styles() {
        if (this.is_resizable) return {};
        return {
          width: `${this.width}px`,
          height: `${this.height}px`,
        };
      },

      inner_label() {
        const { label } = SIMPLE_TOOLS.find((t) => t.id === this.inner_component_props.id) || {};
        return this.label || label;
      },

      inner_component_props() {
        let props = {};

        if (typeof this.inner_component === "number") {
          props.id = this.inner_component;
        } else if (typeof this.inner_component === "object") {
          props = { ...this.inner_component };
        }

        const { item } = SIMPLE_TOOLS.find((t) => t.id === props.id) || {};
        if (item && item.props) {
          // Set deafult props
          props = {
            ...item.props,
            ...props,
            has_wrapper: false,
            unset_draggable_parent: this.unset_draggable,
            set_draggable_parent: this.set_draggable,
            parent_ref: this.parent_ref,
            unique_id: this.unique_id,
          };
        }

        if (this.value_dup) {
          props.value = this.value_dup;
        }

        props.width = this.width;
        props.height = this.height;

        return props;
      },

      inner_component_name() {
        const {
          item: { name },
        } = SIMPLE_TOOLS.find((t) => t.id === this.inner_component_props.id) || {
          item: {},
        };

        return (TOOLS[name] || {}).name;
      },
    },

    data() {
      return {
        // State of focus of current element
        is_edit: false,

        set_default_tool_sizes: {},

        is_filled: false,

        is_editable: true,

        value_dup: null,
      };
    },

    mounted() {
      this.is_editable = this.editable;
      this.is_filled = Boolean(this.value);
      this.value_dup = this.value;
    },

    methods: {
      copy_tool() {
        if (!this.$refs.inner_component) return;
        this.$refs.inner_component.copyTool();
      },
      removeBorder(value) {
        this.value_dup = value;
        this.is_edit = false;
      },
      resetTool() {
        this.is_filled = false;
      },
      resizeProps($event) {
        this.width = $event.width;
        this.height = $event.height;
      },
      clear_tool_sizes($event) {
        this.set_default_tool_sizes = $event;
      },
      isFilled($event) {
        this.resizeProps($event);
        this.is_filled = false;
      },
      async get_value() {
        if (this.$refs.inner_component) {
          const value = await this.$refs.inner_component.get_value();
          return value;
        }
        return null;
      },

      get_inner_component_props() {
        return {
          ...this.inner_component_props,
        };
      },

      unset_draggable() {
        this.$el.removeAttribute("draggable");
      },

      set_draggable() {
        this.$el.setAttribute("draggable", true);
      },

      remove_inner() {
        if (!this.$refs.inner_component) return;
        this.is_filled = false;
        this.value_dup = null;
        this.$refs.inner_component.save_tool_value("");
        this.resizeProps(this.set_default_tool_sizes);
      },

      fill_restriction() {
        if (!this.inner_component_props.id) return;
        this.value = null;
        this.is_filled = true;

        this.$nextTick(() => {
          try {
            this.$refs.inner_component.is_edit = true;
            this.$refs.inner_component.is_editable = true;
            this.is_admin = true;
            this.has_wrapper = true;
            if (this.inner_component_name == "AcEsignToolSignature") this.$refs.inner_component.show_signature();
            if (this.inner_component_name == "AcEsignToolDate") {
              this.width = 106;
              this.height = 41;
              this.$refs.inner_component.save_tool_value(new Date());
            }
          } catch (error) {
            console.log(error);
          }
        });
      },

      /**
       * Preparing props for reusable
       */
      get_props() {
        const tools = [...this.parent_ref.tools];
        const component = tools.filter((tool) => {
          if (tool.unique_id === this.unique_id) {
            return tool;
          }
        })[0];
        const { width, height } = this.$el.getBoundingClientRect();
        const props = {
          width: parseInt(width, 10),
          height: parseInt(height, 10),
          ...component.props,
        };

        return props;
      },

      stop_edit() {
        this.is_edit = false;
      },

      /**
       * Hide this element if signature over
       */
      hide() {
        this.$el.classList.add("hide");
      },

      /**
       * Show this element if signature have deleted
       */
      show() {
        this.$el.classList.remove("hide");
      },
    },
  };
</script>

<style scoped>
  .hide {
    display: none !important;
  }

  .ac-esign-tool-restriction.filled {
    z-index: 2;
  }

  .ac-esign-tool-restriction >>> .ac-esign-tool-wrapper {
    width: 100%;
    height: 100%;
  }

  .ac-esign-tool-restriction_placeholder {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .ac-esign-viewer .ac-esign-viewer_tool {
    position: absolute !important;
    border-radius: 5px !important;
    border: 2px dashed;
  }
</style>
