<template>
  <ToolWrapper
    v-model="is_edit"
    :class="[class_name, 'ac-esign-tool-signature']"
    :resize="is_resize"
    :resize_props="resize_props"
    :editable="is_editable"
    :has_wrapper="has_wrapper"
    :is_admin="is_admin"
    ref="wrapper"
    @enter="save"
    @delete="emit_delete"
    @remove="remove_inner"
    @update:editable="editsign"
    @resize_end="resize_end"
    @copy_tool="copyTool"
  >
    <b-modal
      v-if="!is_filled"
      :title="label == 'Signature' ? 'Signature' : 'Initial'"
      ref="modal"
      size="md"
      no-close-on-esc
      no-close-on-backdrop
      centered
      @shown="set_canvas_props"
      @hidden="on_hidden_modal"
    >
      <keep-alive>
        <b-tabs content-class="mt-3" v-model="active_tab" @input="tabChange">
          <!-- Tab of drawing signature -->
          <b-tab title="Draw" v-if="is_visible_tab(0)">
            <div class="ac-esign-tool-signature_pad">
              <SignaturePad :options="merged_options" width="100%" v-if="show_signature_pad" height="200px" ref="signature_pad" />
              <b-button-group class="mt-2 float-right border-0">
                <b-button @click="undo_signature_pad()" variant="white" size="sm" v-if="this.signature_pad_has_data" v-b-tooltip.hover title="Undo">
                  <span class="fe fe-corner-up-left"></span>
                </b-button>
                <b-button :id="`popover-signature-pad_${_uid}`" @click="popover = !popover" variant="white" size="sm" v-b-tooltip.hover title="Change Color" v-if="this.signature_pad_has_data"
                  ><span class="fe fe-feather"></span>
                </b-button>
                <b-popover
                  v-if="this.signature_pad_has_data"
                  :target="`popover-signature-pad_${_uid}`"
                  :show.sync="popover"
                  triggers="click blur"
                  placement="bottom"
                  custom-class="max-width-100 popover-body-p-0 border-0"
                >
                  <div class="d-flex flex-row">
                    <color-picker @input="on_input_color_picker" v-model="signature_pad_color"></color-picker>
                  </div>
                </b-popover>
              </b-button-group>
              <span v-if="validation_message" class="text-danger">Please add the signature</span>
            </div>
          </b-tab>

          <!-- Tab of uploading signature -->
          <b-tab title="Upload" v-if="is_visible_tab(1)">
            <div class="mb-1" v-if="max_size">
              Max image size is <b>{{ max_size | mb }}</b
              >.
            </div>
            <b-form-file
              :state="file_input_state"
              @submit.stop.prevent
              @input="on_input_file"
              ref="file_input"
              accept="image/*"
              class="mb-2"
              placeholder="Choose a image or drop it here..."
              drop-placeholder="Drop file here..."
            ></b-form-file>
            <b-form-invalid-feedback :state="file_input_state">
              {{ file_input_state_feedback }}
            </b-form-invalid-feedback>
            <span v-if="validation_message" class="text-danger">Please upload the signature</span>
          </b-tab>

          <!-- Tab of typing signature -->
          <b-tab title="Initial" v-if="is_visible_tab(2)">
            <b-form-group label="Signature:" class="mb-2">
              <b-form-input
                @keyup.delete.stop
                v-model="typed_form.value"
                :state="typeof typed_form.value === 'string' && typed_form.value.length > 0"
                type="text"
                required
                placeholder="Enter your initial"
              ></b-form-input>
            </b-form-group>
            <span v-if="validation_message" class="text-danger">Please enter your signature </span>

            <div class="mt-2 mb-2 ac-esign-tool-signature_font-select" v-if="typed_form.value">
              <div
                v-for="font in SIGNATURE_FONTS"
                :key="font"
                :class="{
                  'ac-esign-tool-signature_selected': font === typed_form.font_family,
                }"
                @click="setFontStyle(font)"
              >
                <span
                  :ref="`font_${font}`"
                  :style="{
                    fontFamily: `'${font}'`,
                    fontSize: typed_form.font_size,
                    color: typed_form.color,
                  }"
                  >{{ typed_form.value }}</span
                >
              </div>
            </div>
          </b-tab>
          <template #tabs-end v-if="typed_form.value && is_visible_tab(2)">
            <div class="ml-auto d-flex align-items-center justify-content-end w-25 float-right">
              <b-form-select size="sm" v-model="typed_form.font_size" :options="fonts_size"></b-form-select>
              <b-button size="sm" :id="`popover-form_${_uid}`" class="flex-shrink-0 ml-3" variant="white" @click="popover = !popover" v-b-tooltip.hover title="Change Color"
                ><span class="fe fe-feather"></span
              ></b-button>
              <b-popover :target="`popover-form_${_uid}`" :show.sync="popover" triggers="click blur" placement="bottom" custom-class="max-width-100 popover-body-p-0 border-0">
                <ColorPicker @input="typed_form.color = `rgb(${$event.rgba.r}, ${$event.rgba.g}, ${$event.rgba.b})`" :value="typed_form.color"></ColorPicker>
              </b-popover>
            </div>
          </template>
        </b-tabs>
      </keep-alive>

      <template #modal-footer>
        <div class="d-flex justify-content-end">
          <b-button variant="secondary" class="px-4 mr-3" @click="clear">Clear</b-button>
          <b-button variant="primary" class="px-4" @click="save" :disabled="saving">
            <span v-if="!saving">Save</span>
            <b-spinner small v-else></b-spinner>
          </b-button>
        </div>
      </template>
    </b-modal>

    <div v-if="is_svg && is_filled" ref="svg" class="w-100 h-100"></div>
    <img v-if="!is_svg && is_filled" :src="signature_src" ref="img" alt="Signature" :style="signature_styles" />
  </ToolWrapper>
</template>
<script>
  import { Sketch } from "vue-color";
  import SignaturePad from "vue-signature-pad";
  import ToolWrapper from "./AcEsignToolWrapper.vue";
  import { SIGNATURE_FONTS } from "../../../../../public/sign/fonts";
  import CopyMixin from "../copy-tool";

  const fonts_size = [
    { text: 9, value: "9pt" },
    { text: 10, value: "10pt" },
    { text: 11, value: "11pt" },
    { text: 12, value: "12pt" },
    { text: 14, value: "14pt" },
    { text: 16, value: "16pt" },
    { text: 18, value: "18pt" },
    { text: 20, value: "20pt" },
    { text: 22, value: "22pt" },
    { text: 24, value: "24pt" },
    { text: 26, value: "26pt" },
    { text: 28, value: "28pt" },
    { text: 36, value: "36pt" },
  ];

  /**
   * Ac Esign Tool Signature - tool for draw signature
   *
   * @requires SignaturePad
   * @requires ToolWrapper
   * @requires vue-color
   */
  export default {
    name: "AcEsignToolSignature",

    mixins: [CopyMixin],

    components: {
      ToolWrapper,
      SignaturePad,
      ColorPicker: Sketch,
    },

    props: {
      label: {
        type: String,
      },
      options: Object,

      tab: Number,

      // Max image size
      max_size: {
        type: Number,
        required: false,
      },

      // Assigned restriction
      restriction: {
        type: Object,
        required: false,
      },

      value: {
        type: String,
      },

      class_name: [String, Array, Object],

      // Signature pad width
      width: {
        type: Number,
        default: 350,
      },

      // Signature pad height
      height: {
        type: Number,
        default: 300,
      },

      editable: {
        type: Boolean,
        default: false,
      },

      is_admin: {
        type: Boolean,
        default: false,
      },

      has_wrapper: {
        type: Boolean,
        default: true,
      },

      unset_draggable_parent: {
        type: Function,
      },

      set_draggable_parent: {
        type: Function,
      },
      parent_ref: Object,
      unique_id: String,
    },

    filters: {
      mb(value) {
        return `${(value / 1024 / 100).toFixed(3) * 100}MB`;
      },
    },

    computed: {
      merged_options() {
        return {
          ...this.options,
          ...this.changed_options,
        };
      },

      has_signature() {
        return Boolean(this.signature_src);
      },

      img_styles() {
        if (!this.is_svg && this.is_resize) return {};
        return {
          width: `${this.resize_props.width}px`,
          height: `${this.resize_props.height}px`,
        };
      },

      is_svg() {
        return this.signature_src && /(data:image\/svg\+xml;base64,)/.test(this.signature_src);
      },

      signature_styles() {
        if (!this.is_svg && this.is_resize) return {};
        return {
          width: `${this.resize_props.width}px`,
          height: `${this.resize_props.height}px`,
        };
      },

      minWidthModal() {
        return 500;
      },
    },

    data() {
      return {
        count: 0,
        clear_tool_sizes: {},
        validation_message: false,
        is_filled: false,
        is_editable: false,
        is_resize: true,
        popover: false,
        saving: false,
        is_edit: false,
        show_signature_pad: false,
        signature_src: null,
        signature_pad_color: this.options.penColor,
        signature_src_preview: null,
        file_input_state: null,
        file_input_state_feedback: null,
        typed_form: {
          color: "rgb(0, 0, 0)",
          value: null,
          font_size: fonts_size[3].value,
          font_family: Object.keys(SIGNATURE_FONTS)[0],
        },
        // Options of signature pad
        changed_options: {
          onBegin: this.onBegin,
        },
        resize_props: {
          width: this.width,
          height: this.height,
        },
        active_tab: 0,
        SIGNATURE_FONTS: Object.keys(SIGNATURE_FONTS),
        fonts_size,
        signature_pad_has_data: false,
      };
    },

    mounted() {
      this.clear_tool_sizes = { ...this.resize_props };
      this.$emit("clear_tool_sizes", this.clear_tool_sizes);
      this.is_editable = this.editable;
      this.is_filled = Boolean(this.value);
      if (this.value && this.value.includes("image/svg+xml")) {
        this.signature_src = this.value;
        this.$nextTick(() => {
          this.$refs.svg.innerHTML = window.atob(this.signature_src.split(",").splice(-1)[0]);
        });
      } else {
        this.signature_src = this.value;
      }
    },

    methods: {
      setFontStyle(font) {
        this.typed_form.font_family = font;
      },
      tabChange($event) {
        if ($event == 2) this.setFontStyle("Festive");
        this.typed_form.value = null;
        this.validation_message = false;
      },
      remove_inner() {
        if (this.is_editable) {
          if (this.$refs.svg) this.$refs.svg.innerHTML = "";
          this.$nextTick(() => {
            this.is_filled = false;
            this.signature_src = null;
            this.signature_pad_has_data = false;
            this.signature_src_preview = null;
            this.typed_form = {
              color: "rgb(0, 0, 0)",
              value: null,
              font_size: fonts_size[3].value,
              font_family: Object.keys(SIGNATURE_FONTS)[0],
            };
          });
        }
        this.save_tool_value("");
      },

      editsign(e) {
        this.is_editable = e;
        if (!this.is_editable) {
          this.unset_draggable();
        } else {
          this.set_draggable();
        }
      },

      show_signature() {
        this.show_signature_pad = false;
        this.unset_draggable();
        Object.keys(this.typed_form).forEach((key) => {
          key == "font_size" ? (this.typed_form[key] = "20pt") : key == "color" ? (this.typed_form[key] = "rgb(0, 0, 0)") : (this.typed_form[key] = null);
        });
        this.setFontStyle("Festive");
        this.validation_message = false;
        this.count = 0;
        this.changed_options = { onBegin: this.onBegin };
        this.signature_pad_color = "#000";
        setTimeout(() => {
          this.show_signature_pad = true;
          this.$refs.modal.show();
        }, 50);
      },

      async get_value() {
        if (/^(data:image\/svg\+xml;base64)/.test(this.signature_src)) {
          // Display initial component in preview scenario
          Object.keys(this.typed_form).forEach((key) => {
            if (key == "font_size") this.typed_form[key] = this.$refs.svg.firstElementChild.firstElementChild.getAttributeNode("font-size").value;
            else if (key == "font_family") this.typed_form[key] = this.$refs.svg.firstElementChild.firstElementChild.getAttributeNode("font-family").value;
            else if (key == "color") this.typed_form[key] = this.$refs.svg.firstElementChild.firstElementChild.getAttributeNode("fill").value;
            else this.typed_form[key] = this.$refs.svg.innerText;
          });
          return {
            type: "svg",
            ...this.typed_form,
          };
        }
        return this.signature_src;
      },

      /**
       * Preparing props for reusable
       */
      get_props() {
        const tools = [...this.parent_ref.tools];
        const component = tools.filter((tool) => {
          if (tool.unique_id === this.unique_id) {
            return tool;
          }
        })[0];
        return { ...component.props };
      },

      is_visible_tab(tab_index) {
        if (typeof this.tab !== "number") return true;
        this.active_tab = this.tab;
        return tab_index === this.tab;
      },

      on_hidden_modal() {
        // this.clear();
        if (!this.has_signature) {
          this.is_filled = Boolean(this.value);
          this.$emit("isFilled", this.clear_tool_sizes);
        }
      },

      /**
       * Remove current tool
       */
      delete_tool() {
        this.emit_delete();
        this.$destroy();
      },

      emit_delete() {
        this.$emit("delete");
      },

      /**
       * Undo signature
       */
      undo_signature_pad() {
        this.$refs.signature_pad.undoSignature();
        if (this.$refs.signature_pad.isEmpty()) {
          this.signature_pad_has_data = false;
        }
        this.changed_options = {
          penColor: this.$refs.signature_pad.options.penColor,
        };
      },

      /**
       * Save drawn signature
       */
      async save() {
        try {
          let value = null;
          switch (this.active_tab) {
            case 0: {
              if (!this.$refs.signature_pad) return;
              const { data, isEmpty } = this.$refs.signature_pad.saveSignature();
              if (isEmpty) {
                this.validation_message = true;
                return;
              } else value = data;
              this.$refs.signature_pad.resizeCanvas();
              break;
            }
            case 1: {
              if (!this.signature_src_preview) {
                this.validation_message = true;
                return;
              } else value = this.signature_src_preview;

              break;
            }
            default: {
              if (!this.typed_form.value) {
                this.validation_message = true;
                setTimeout(() => (this.validation_message = false), 1500);
                return;
              } else {
                this.saving = true;
                const rects = this.$refs[`font_${this.typed_form.font_family}`][0].getBoundingClientRect();
                const svg = `
            <svg viewBox="0 0 ${rects.width} ${rects.height}" class="w-100 h-100">
                <text
                    x="0"
                    y="${Math.floor(parseInt(this.typed_form.font_size, 10) * 1.33)}"
                    font-family="${this.typed_form.font_family}"
                    font-size="${this.typed_form.font_size}"
                    fill="${this.typed_form.color}"
                >${this.typed_form.value}</text>
            </svg>
          `;
                const div = document.createElement("div");
                const inner_tag = svg.trim();
                div.innerHTML = inner_tag;
                this.$nextTick(() => {
                  this.$refs.svg.innerHTML = inner_tag;
                  this.resize_props = {
                    width: rects.width,
                    height: rects.height,
                  };
                  this.$emit("resize-props", this.resize_props);
                });
                const s = new XMLSerializer().serializeToString(div.firstChild);
                value = `data:image/svg+xml;base64,${window.btoa(s)}`;

                break;
              }
            }
          }
          if (this.label == "Signature") this.$refs.signature_pad.resizeCanvas();
          this.signature_src = value;
          this.$nextTick(() => {
            this.save_tool_value(this.signature_src);
          });
          this.is_edit = this.saving = false;
          this.$refs.modal.hide();
          this.is_filled = Boolean(value);

          this.set_draggable();
        } catch (error) {
          console.log(error);
        }
      },

      save_tool_value(val) {
        const tools = [...this.parent_ref.tools];
        this.parent_ref.tools = tools.map((tool) => {
          if (tool.unique_id === this.unique_id && tool.component.inner_component_name == "AcEsignToolSignature") {
            tool.props.value = val;
            tool.props.width = this.resize_props.width;
            tool.props.height = this.resize_props.height;
          }
          return tool;
        });
        this.$emit("remove_border", val);
      },

      clear() {
        this.signature_src_preview = null;

        if (this.$refs.signature_pad) {
          this.$refs.signature_pad.clearSignature();
          this.signature_pad_has_data = false;
        }
        if (this.$refs.file_input) {
          this.$refs.file_input.reset();
        }

        this.$nextTick(() => {
          this.file_input_state = null;
        });
        Object.keys(this.typed_form).forEach((key) => {
          // this.typed_form[key] = null;
          key == "font_size" ? (this.typed_form[key] = "20pt") : key == "color" ? (this.typed_form[key] = "rgb(0, 0, 0)") : (this.typed_form[key] = null);
        });
        this.setFontStyle("Festive");
        this.$nextTick(() => {
          this.$refs.signature_pad.resizeCanvas();
        });
      },

      /**
       * Unset dragging for current tool when tool is mounted
       * After saving signature tool is available to dragging
       */
      unset_draggable() {
        this.$el.removeAttribute("draggable");
        if (this.unset_draggable_parent) {
          this.unset_draggable_parent();
        }
      },

      set_draggable() {
        this.$el.setAttribute("draggable", true);
        if (this.set_draggable_parent) {
          this.set_draggable_parent();
        }
      },

      /**
       * Set canvas width and height of signature pad
       */
      set_canvas_props() {
        if (this.label == "Signature") this.$refs.signature_pad.resizeCanvas();
      },

      on_input_file(file) {
        if (!file || !/^(image\/)/.test(file.type)) {
          this.file_input_state = false;
          this.signature_src_preview = null;
          this.file_input_state_feedback = "You must upload image";
          return;
        }

        if (file.size / 1024 > this.max_size) {
          this.file_input_state = false;
          this.signature_src_preview = null;
          this.file_input_state_feedback = "Image size is larger than allowed";
          return;
        }

        this.file_input_state = true;

        const reader = new FileReader();

        reader.onloadend = () => {
          this.signature_src_preview = reader.result;
        };

        if (file) {
          reader.readAsDataURL(file);
        }
      },

      on_input_color_picker({ rgba }) {
        this.changed_options = {
          penColor: `rgba(${rgba.r}, ${rgba.g}, ${rgba.b}, ${rgba.a})`,
        };
      },
      onBegin() {
        this.signature_pad_has_data = true;
        if (this.count == 0) {
          this.$refs.signature_pad.resizeCanvas();
          this.count++;
        }
      },

      resize_end(data) {
        const tools = [...this.parent_ref.tools];
        this.parent_ref.tools = tools.map((tool) => {
          if (tool.unique_id === this.unique_id) {
            tool.props.width = data.width;
            tool.props.height = data.height;
          }
          return tool;
        });
      },
    },
  };
</script>

<style scoped>
  @import "../../../../../public/sign/fonts/signature-fonts.css";

  .ac-esign-tool-signature img {
    pointer-events: none;
  }

  .ac-esign-tool-signature .tab-pane {
    outline: none;
  }

  .ac-esign-tool-signature_inner,
  .ac-esign-tool-signature img {
    width: 100%;
    height: 100%;
  }

  .ac-esign-tool-signature_pad > div:not(.d-flex) {
    border: 1px solid rgba(0, 0, 0, 0.19);
    border-radius: 4px;
  }
  .ac-esign-tool-signature_pad > div:first-child {
    margin: 0 auto;
  }

  .ac-esign-tool-signature_font-select {
    display: flex;
    flex-wrap: wrap;
  }

  .ac-esign-tool-signature_font-select > div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 1.5rem;
    border: 1px solid rgba(0, 0, 0, 0.19);
    width: calc(100% / 3);
    cursor: pointer;
  }

  .ac-esign-tool-signature_font-select > div.ac-esign-tool-signature_selected {
    box-shadow: inset 0 0 10px 0 #cecece;
  }

  .ac-esign-tool-signature_font-select > div span:empty:before {
    content: "Enter your initial";
    opacity: 0.5;
  }
  .ac-esign-tool_placeholder {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.7;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
</style>
