<template>
  <div>
    <div class="ac-esign" v-if="render">
      <div class="ac-esign_nav d-md-flex align-items-center justify-content-between">
        <div class="d-flex align-items-center ac-esign-toolbar py-2">
          <!-- <b-button variant="link" class="no-focus ml-2 mt-2" @click="show_toolbar('menu')" v-b-tooltip:hover title="Click to get the pages">
            <span :class="['fe', 'fe-book-open', toolbar_show ? 'text-muted' : 'text-primary']"></span>
          </b-button>
          <b-button variant="link" class="no-focus btn-link mt-2" @click="show_toolbar('settings')" v-b-tooltip:hover title="Click to edit the PDF">
            <span :class="['fe', 'fe-settings', !toolbar_show ? 'text-muted' : 'text-primary']"></span>
          </b-button> -->
          <b-img src="/img/sign/logo.svg" alt="logo" class="pl-3" fluid></b-img>
        </div>
        <div class="d-flex mt-2 mt-md-0" :class="toolbar_show && !is_public ? 'justify-content-between' : 'justify-content-end'">
          <b-dropdown variant="outline-secondary min-width-10 ml-3 ml-md-4 d-flex mr-3" v-if="toolbar_show && !is_public" size="sm">
            <template v-slot:button-content>
              <div class="ac-esign-toolbar_user-select">
                <div class="ac-esign-toolbar_user-color" :style="`background-color:${selected_user.color}`"></div>
                {{ selected_user.name }}
              </div>
            </template>
            <b-dropdown-item
              v-for="(option, index) in toolbar_users_list"
              :key="`option_${index}`"
              :active="option === selected_user"
              class="ac-esign-toolbar_user-select_item custom-select-sm"
              @click="select_user(option)"
            >
              <div class="ac-esign-toolbar_user-color" :style="`background-color:${option.color}`"></div>
              {{ option.text }}
            </b-dropdown-item>
          </b-dropdown>
          <div>
            <span class="mr-3 text-dark" v-if="$parent.document_signees.status == 'Signature Pending'"
              >Signatures pending from others, you will be able to preview and download it when everyone is done signing.</span
            >
            <b-button variant="primary" class="badge-soft-primary mr-3" size="sm" @click="preview_pdf" :disabled="$parent.document_signees.status == 'Signature Pending'">Preview</b-button>
            <b-button class="badge-soft-primary mr-3" size="sm" @click="$emit('show_comments')">Comments </b-button>
          </div>
        </div>

        <!-- For now we are hiding the fullscreen feature -->
        <!--  Fullscreen button    -->
        <!-- <b-button
            variant="secondary"
            size="sm"
            class="no-focus ml-3 mr-3"
            @click="toggle_fullscreen"
            v-b-tooltip:hover
            :title="fullscreen ? 'Click to get the full screen' : 'Click to close the full screen'"
          >
            <span :class="['fe', 'text-white', fullscreen ? 'fe-maximize' : 'fe-minimize']"></span>
          </b-button> -->
      </div>

      <div class="ac-esign_body">
        <div class="ac-esign_body-toolbar d-none d-lg-flex">
          <!-- Ac Esign Toolbar  -->
          <AcEsignToolbar
            v-show="toolbar_show"
            ref="ac-esign-toolbar"
            class="ac-esign_toolbar"
            :is_public="is_public"
            :tools="toolbar_tools"
            :users_list="select_user_options"
            :active_users_list="active_users_list"
            :show_save_button="show_save_button"
            v-model="selected_user"
            @save="save"
            @preview="preview_pdf"
            @upload="upload_pdf"
          />

          <!-- Ac Esign Thumbnails  -->
          <AcEsignThumbnails v-show="!toolbar_show" class="ac-esign_thumbnails" :pdfjs_document="pdfjs_document" :active_page="active_page" @go_to="scroll_into_page">
            <template #loader>
              <slot name="thumbnails_loader">loading thumbnails...</slot>
            </template>
          </AcEsignThumbnails>
        </div>

        <!-- Ac Esign Viewer  -->
        <div class="ac-esign_viewer_container d-flex justify-content-start justify-content-md-center">
          <AcEsignViewer
            :pdfjs_document="pdfjs_document"
            :scale="selected_pdf_scale"
            :active_users_list="active_users_list"
            :selected_user="selected_user"
            @active_page="active_page = $event"
            @pagesrendered="on_pages_rendered"
            ref="viewer"
          >
            <template #loader>
              <slot name="viewer_loader">loading viewer...</slot>
            </template>
          </AcEsignViewer>

          <transition name="fade">
            <div v-show="converting" class="ac-esign_process_loader">
              <div>
                <slot name="process_loader" v-if="$slots.process_loader">process...</slot>
              </div>
            </div>
          </transition>
        </div>
      </div>

      <b-modal class="position-absolute" ref="preview_modal" size="xl" :title="s3_config && s3_config.name ? `Preview of ${s3_config.name}` : `Preview`" hide-footer>
        <iframe class="ac-esign_preview" :src="preview_pdf_src"></iframe>
      </b-modal>
    </div>
  </div>
</template>

<script>
  import { getDocument } from "pdfjs-dist";
  import AcEsignViewer from "./AcEsignViewer.vue";
  import AcEsignToolbar from "./AcEsignToolbar";
  import AcEsignThumbnails from "./AcEsignThumbnails";
  import AcS3Uploader from "@/mixins/ac-s3-uploader";
  import { edit_pdf } from "./utils";
  import { SIMPLE_TOOLS, RESTRICTION_TOOLS, DEFAULT_TOOLS } from "./constants";
  /**
   * ac-esign
   * @inherits AcS3Uploader
   *
   * @requires getDocument
   * @requires AcEsignViewer
   * @requires AcEsignToolbar
   * @requires AcEsignThumbnails
   * @requires AcS3Uploader
   * @requires edit_pdf
   * @requires SIMPLE_TOOLS, RESTRICTION_TOOLS
   *
   * @event input - used tools
   * @param [{ id: number, ... }]
   *
   * @event uploaded - result of uploading pdf document
   * @param {string}
   */
  export default {
    name: "AcEsign",
    mixins: [AcS3Uploader],
    components: {
      AcEsignViewer,
      AcEsignToolbar,
      AcEsignThumbnails,
    },
    props: {
      /**
       * Disable users dropdown & tools
       */
      is_public: {
        type: Boolean,
        required: false,
        default: false,
      },
      /**
       * Disable/Enable component
       */
      render: {
        type: Boolean,
        required: false,
        default: true,
      },
      /**
       * Source of pdf document
       */
      src: {
        type: String,
        required: true,
      },
      /**
       * Maximum upload image size on pdf docment
       */
      max_image_size: {
        type: Number,
        required: false,
        default: 2048,
      },
      /**
       * s3 config object for uploading
       */
      s3_config: {
        type: Object,
        required: true,
      },
      /**
       * Admin mode
       * Provides access to additional functionality
       */
      admin: {
        type: Boolean,
        default: false,
      },
      /**
       * Tools on pdf document
       * @param value - array<{ name: string, page_index: number,
       * offsetX: number, offsetY: number, props: object }>
       */
      value: {
        type: Array,
        required: false,
        default: () => [],
      },
      /**
       * List of user
       * Admin can assing tool for specific user (other users can't see that tool)
       * @param users_list - array<{ id: number, ... }>
       */
      users_list: {
        type: Array,
        required: false,
        default: () => [],
      },
      /**
       * List of active users
       * @param active_users_list - array<{ id: number, ... }>
       */
      active_users_list: {
        type: Array,
        required: false,
        default: () => [],
      },
      /**
       * List of editable tools
       */
      edit_tools: {
        type: Array,
        default: () => [],
        validator: (tools) => !tools.some((tool) => !["text", "date", "signature"].includes(tool)),
      },
      select_colors: {
        type: Array,
        default: () => [],
      },
      /**
       * Admin mode
       * Provides access to additional functionality
       */
      show_save_button: {
        type: Boolean,
        default: true,
      },
    },
    data() {
      return {
        // Instance of pdfjs-dist lib
        pdfjs_document: {},
        // State of converting pdf document
        converting: false,
        toolbar_show: false,
        active_page: 1,
        // Base64 value of converted pdf document
        preview_pdf_src: null,
        fullscreen: true,
        selected_pdf_scale: 1,
        zoom_select_options: [
          { text: "Original", value: 1 },
          { text: "Fit page size", value: "fit" },
          { text: "50%", value: 0.5 },
          { text: "75%", value: 0.75 },
          { text: "120%", value: 1.2 },
          { text: "150%", value: 1.5 },
          { text: "200%", value: 2 },
          { text: "300%", value: 3 },
          { text: "400%", value: 4 },
        ],
        tools: [],
        toolsCount: 0,
        selected_user: null,
        select_user_options: [],
      };
    },
    computed: {
      /**
       * Identify select user options
       */
      toolbar_users_list() {
        let list = this.select_user_options;
        if (!this.admin) {
          list = [this.selected_user];
        }
        return list;
      },
      /**
       * Identify scale from options or scale is custom
       */
      is_contain_scale() {
        return this.zoom_select_options.map((opt) => opt.value).some((val) => val === this.selected_pdf_scale);
      },
      /**
       * Prepare tool options for toolbar
       * Depends on 'admin'
       */
      toolbar_tools() {
        const toolbar_tools = { add: [], restrictions: [] };
        const signature_tool = SIMPLE_TOOLS.find((t) => t.item.name === "signature");
        signature_tool.item.props.max_size = this.max_image_size;
        toolbar_tools.add = [...SIMPLE_TOOLS];
        if (this.admin) {
          toolbar_tools.restrictions = [...RESTRICTION_TOOLS];
        }
        return toolbar_tools;
      },
    },
    created() {
      this.tools = this.value.map((t) => {
        t.unique_id = `tool_${this.toolsCount + 1}`;
        this.toolsCount = this.toolsCount + 1;
        return t;
      });
      const create_color = () => {
        const letters = "0123456789ABCDEF";
        let color = "#";
        for (let i = 0; i < 6; i += 1) {
          color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
      };
      this.select_user_options = this.users_list.map((user, index) => ({
        ...user,
        text: user.name,
        color: !this.is_public ? this.select_colors[index] || create_color() : "#fff",
      }));
      let list = this.select_user_options;
      if (!this.admin) {
        const current_user = this.active_users_list[0];
        list = this.select_user_options.filter((option) => option.id === current_user.id);
      }
      const [first_option] = list;
      this.selected_user = first_option;
    },
    mounted() {
      console.log("usersOne", this.active_users_list);
      this.load_pdf();
      this.add_fullscreen_listener();
    },
    methods: {
      /**
       * send selected user data to toolbar
       */
      select_user(option) {
        this.selected_user = option;
      },
      /**
       *To show Menu or Thumbnails
       */
      show_toolbar(icon) {
        return icon == "menu" ? (this.toolbar_show = false) : (this.toolbar_show = true);
      },
      /**
       * Fires when pages is rendered
       */
      on_pages_rendered() {
        this.load_tools();
      },
      /**
       * Initialize transferred tools
       */
      load_tools(duplicate) {
        const tools = this.tools
          .map((t) => {
            const default_tool_props = DEFAULT_TOOLS.find((dt) => dt.id === t.id);
            if (!default_tool_props) return null;
            let editable = this.admin || (t.user_id && this.active_users_list.some((u) => u.id === t.user_id));
            if (t.props && typeof t.props.editable !== "undefined") {
              editable = editable && ((!t.props.value && !duplicate) || (t.props.value && duplicate) || (!t.props.value && duplicate));
            }
            const tool = {
              ...default_tool_props.item,
              ...t,
              props: {
                ...(default_tool_props.item.props || {}),
                ...(t.props || {}),
                editable,
              },
            };
            return tool;
          })
          .filter((t) => !!t);
        tools.forEach((tool) => {
          this.$refs.viewer.add_tool(tool, tool, true, duplicate);
        });
      },
      /**
       * Add 'fullscreenchange' listener for change icon
       */
      add_fullscreen_listener() {
        this.$el.addEventListener("fullscreenchange", () => {
          this.fullscreen = !this.fullscreen;
        });
      },
      /**
       * Load pdf document and initialize PDF.js instance
       */
      load_pdf() {
        getDocument(this.src)
          .promise.then((pdf) => {
            this.pdfjs_document = pdf;
          })
          .catch((err) => {
            console.log("err", err);
          });
      },
      /**
       * Zoom in scale of current pdf document
       */
      zoom_in() {
        this.selected_pdf_scale = this.selected_pdf_scale * 1.1;
      },
      /**
       * Zoom out scale of current pdf document
       */
      zoom_out() {
        this.selected_pdf_scale = this.selected_pdf_scale / 1.1;
      },
      /**
       * Change scale of current pdf document
       * @param {number} value - scale value
       */
      change_pdf_scale(value) {
        if (typeof value === "number") {
          this.selected_pdf_scale = value;
        } else if (value === "fit") {
          this.selected_pdf_scale = this.$refs.viewer.$el.getBoundingClientRect().width / this.$refs.viewer.pages_elements[0].getBoundingClientRect().width;
        }
      },
      /**
       * Scroll to page element
       * @param {number} page_index
       */
      scroll_into_page(page_index) {
        this.active_page = page_index;
        this.$refs.viewer.scroll_into_page(page_index);
      },
      /**
       * Saving pdf file to s3
       */
      async upload_pdf() {
        this.converting = true;
        const edited_pdf = await edit_pdf({
          src: this.src,
          tools: this.tools,
          pdf_name: this.s3_config && this.s3_config.name ? this.s3_config.name : "Preview",
        });
        this.converting = false;
        try {
          const res = await this.upload_to_s3(edited_pdf);
          this.$emit("uploaded", res);
        } catch (e) {
          console.error(e);
        }
      },
      /**
       * Open preview modal
       */
      async preview_pdf() {
        this.converting = true;
        const preview_pdf = await edit_pdf({
          src: this.src,
          tools: this.tools,
          // as_b64: true, We are commenting this line because dataURI format is not supporting for large files.
          pdf_name: this.s3_config && this.s3_config.name ? this.s3_config.name : "Preview",
        });
        this.converting = false;

        //  We are converting Blob data into Blob URL for supporting large files.
        this.preview_pdf_src = URL.createObjectURL(preview_pdf);

        this.$refs.preview_modal.show();
      },
      /**
       * Toggle browser to full screen mode
       */
      toggle_fullscreen() {
        if (this.fullscreen) {
          this.$el.requestFullscreen();
        } else {
          document.exitFullscreen();
        }
      },
      /**
       * Get used tools and make them reusable
       * Emit input event
       */
      async save() {
        let used_tools = this.tools.map(
          (tool) =>
            new Promise((resolve) => {
              const props = tool.component.get_props ? tool.component.get_props() : {};
              resolve({
                id: tool.id,
                user_id: tool.user_id,
                page_index: tool.page,
                offsetX: tool.component.$el.offsetLeft || parseInt(tool.component.$el.style.left, 10),
                offsetY: tool.component.$el.offsetTop || parseInt(tool.component.$el.style.top, 10),
                props: {
                  ...props,
                },
              });
            }),
        );
        used_tools = await Promise.all(used_tools);
        this.$emit("input", used_tools);
        return used_tools;
      },
    },
    watch: {
      render: {
        handler(newvalue) {
          this.render = newvalue;
        },
      },
      deep: true,
    },
  };
</script>
