<template>
  <ToolWrapper
    v-model="is_edit"
    :class="[class_name, 'ac-esign-tool-date']"
    :editable.sync="is_editable"
    :has_wrapper="has_wrapper"
    :is_admin="is_admin"
    ref="wrapper"
    @enter="stop_edit"
    @remove="remove_inner"
    @copy_tool="copyTool"
  >
    <DateRangePicker v-if="is_edit" v-bind="data_range_props" :date-range="date_range_value" @update="on_update_date">
      <template #input="{ startDate }" style="min-width: 350px">
        {{ startDate | date(data_range_props.localeData.format) }}
      </template>
    </DateRangePicker>
    <div v-if="!is_edit" :class="!is_edit ? 'date' : ''">
      {{ date_range_value.startDate | date(data_range_props.localeData.format) }}
    </div>
  </ToolWrapper>
</template>

<script>
  import DateRangePicker from "vue2-daterange-picker";
  import moment from "moment";
  import ToolWrapper from "./AcEsignToolWrapper.vue";
  import "vue2-daterange-picker/dist/vue2-daterange-picker.css";
  import CopyMixin from "../copy-tool";

  const default_config = {
    opens: "center",
    showDropdowns: true,
    ranges: false,
    singleDatePicker: true,
    localeData: {
      format: "MM-DD-YYYY",
    },
    minDate: moment().format("L"),
  };

  /**
   * Ac Esign Tool Date - tool of input date
   *
   * @requires DateRangePicker
   * @requires moment
   * @requires ToolWrapper
   */
  export default {
    name: "AcEsignToolDate",

    mixins: [CopyMixin],

    components: {
      DateRangePicker,
      ToolWrapper,
    },

    props: {
      class_name: [String, Array, Object],

      value: {
        type: Date,
      },

      // Config of ac-date-range-picker
      config: {
        type: Object,
        default: () => ({}),
      },

      editable: {
        type: Boolean,
        default: false,
      },

      is_admin: {
        type: Boolean,
        default: false,
      },

      has_wrapper: {
        type: Boolean,
        default: true,
      },
      parent_ref: Object,
      unique_id: String,
    },

    filters: {
      date(value, format) {
        return moment(value).format(format);
      },
    },

    computed: {
      data_range_props() {
        return {
          ...default_config,
          ...this.config,
          disabled: !this.is_editable,
        };
      },

      date_range_value() {
        let value = this.input_value || this.value;
        if (!value) value = new Date();

        return {
          startDate: value,
          endDate: value,
        };
      },
    },

    data() {
      return {
        // State of focus of current element
        is_edit: false,

        // Value of current input
        input_value: null,

        is_editable: false,
        clear_tool_sizes: {},
        resize_props: {
          width: 106,
          height: 41,
        },
      };
    },

    mounted() {
      if (this.$parent?.resize_props) {
        this.clear_tool_sizes = { ...this.$parent?.resize_props };
        this.$emit("clear_tool_sizes", this.clear_tool_sizes);
      }
      this.is_editable = this.editable;
      if (!this.value) this.input_value = new Date();
      this.$nextTick(() => {
        if (this.unique_id) this.save_tool_value(this.value || this.input_value);
      });
    },

    methods: {
      async get_value() {
        return moment(this.date_range_value.startDate).format(this.data_range_props.localeData.format);
      },

      get_props() {
        const tools = [...this.parent_ref.tools];
        const component = tools.filter((tool) => {
          if (tool.unique_id === this.unique_id) {
            return tool;
          }
        })[0];
        component.props["name"] = "add_tool";
        return { ...component.props };
      },

      stop_edit() {
        this.is_edit = false;
      },

      on_update_date(event) {
        this.stop_edit();
        this.input_value = event.startDate;
        this.$emit("resize-props", { width: 106, height: 41 });
        this.save_tool_value(event.startDate);
      },

      remove_inner() {
        if (this.is_editable) this.input_value = new Date();

        this.save_tool_value();
      },

      save_tool_value(val) {
        const tools = [...this.parent_ref.tools];
        this.parent_ref.tools = tools.map((tool) => {
          if (tool.unique_id === this.unique_id) {
            if (val) tool.props.value = val;
            else delete tool.props.value;
            tool.props.width = 106;
            tool.props.height = 41;
          }
          return tool;
        });

        this.$emit("remove_border", val);
      },
    },
  };
</script>
<style>
  .ac-esign-tool-date .date {
    padding: 7px 10px;
    line-height: 1.5;
    border: 1px solid #fff;
    width: 106px;
    height: 41px;
  }
  .reportrange-text {
    border: none !important;
  }
  .vue-daterange-picker .dropdown-menu {
    padding: 0;
    top: 40px;
  }
  .vue-daterange-picker {
    position: relative;
    display: inline-block;
    min-width: 106px;
    margin: auto;
  }
</style>
