import Data from "@/js/core/data";
const Documents_Server = "https://api.${region}.500apps.com/sign/v1/public/";

class Documents extends Data {
  constructor() {
    super(Documents_Server);
    this.disablePCors();
    this.limit = 1;
  }

  async get_document_signees(document_id, domain_id) {
    // eslint-disable-next-line
    let url = `${Documents_Server}${domain_id}/${document_id}`;
    this.url = eval("`" + url + "`");

    return await super.get();
  }

  async update_document_data(project_id, domain_id, document_id, contact_id, form, created_by) {
    // eslint-disable-next-line
    let url = `${Documents_Server}${project_id}/${domain_id}/${created_by}/${document_id}/${contact_id}`;
    this.url = eval("`" + url + "`");

    return await super.edit(document_id, form);
  }

  // Get document expiry details
  async get_document_expiry_details(domain_id, user_id, document_id) {
    // eslint-disable-next-line
    let url = `${Documents_Server}${domain_id}/${user_id}/${document_id}/expiry`;
    this.url = eval("`" + url + "`");

    return await super.get();
  }
}
export default new Documents();
