import axios from "axios";

const get_presigned_url = async (url, file) => {
  const config = {
    withCredentials: true,
    credentials: "include",
  };

  const response = await axios.get(url + file.name, config);
  return response && response.data;
};

export default {
  props: {
    /**
     * S3 config object
     * @param {object} s3_config
     * @label s3_config
     * @category_name 3_Advanced
     */
    s3_config: Object,
  },

  data() {
    return {
      cancel_tokens: {},
      current_upload_status: {
        showProgress: true,
      },
      presigned_urls: {},
    };
  },

  methods: {
    // Returns Uploading file Progress
    emit_progress: function(view, loaded, percentage) {
      this.current_upload_status = {
        showProgress: view,
        loaded: percentage,
        loaded_bytes: loaded,
      };
    },
    /**
     * Uploading file to s3
     * @param file
     * @returns {Promise<String>} - Uploaded file URL
     */
    async upload_to_s3(file) {
      if (!file) return;

      // Set file name if hasn't name
      if (!file.name) {
        file.name = `${this.s3_config.name || "file"}.${file.type.split("/")[1]}`;
      }

      const { signed_url, File_link } = await get_presigned_url(this.s3_config.url, file);

      this.presigned_urls[signed_url] = file;
      let config = {
        headers: {
          "Content-Type": file.type,
        },
        cancelToken: new axios.CancelToken((c) => {
          this.cancel_tokens[signed_url] = c;
        }),
        onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          if (percentCompleted == 100) {
            this.emit_progress(true, progressEvent.loaded, percentCompleted);
            setTimeout(() => {
              this.emit_progress(false, progressEvent.loaded, percentCompleted);
            }, 1000);
          } else {
            this.emit_progress(true, progressEvent.loaded, percentCompleted);
          }
        },
      };
      // eslint-disable-next-line
      const response = await axios.put(signed_url, file, config);
      this.presigned_urls[signed_url] = null;
      return File_link;
    },
  },
};
